<template>
  <section>
    <!---->
    <el-table :data="gateEvents" highlight-current-row v-loading="listLoading" style="width: 100%;">
      <el-table-column type="index" width="40" align="center"></el-table-column>

      <el-table-column
        property="srcTs"
        label=""
        width="110"
        sortable
        :formatter="formatTime"
        align="center"
      ></el-table-column>
      <el-table-column property="lockCat" label="" width="120" sortable align="center"></el-table-column>
      <el-table-column property="media" label="" width="120" sortable align="center"></el-table-column>
      <el-table-column property="userName" label="" width="120" sortable align="center"></el-table-column>
      <!-- <el-table-column property="homeName" label="" width="180" sortable align="center"></el-table-column> -->
      <el-table-column property="deviceName" label="" width="180" sortable align="center"></el-table-column>
      <!-- <el-table-column
        property="envTemp"
        label="()"
        width="120"
        sortable
        align="center"
        v-if="showTemp"
      ></el-table-column>-->
      <el-table-column
        property="humanTemp"
        label="()"
        width="120"
        sortable
        align="center"
        v-if="showTemp"
      ></el-table-column>
      <el-table-column
        property="accountNickName"
        label=""
        width="120"
        sortable
        align="center"
        v-if="isAdmin===1"
      ></el-table-column>
      <el-table-column prop="listPictureName" label="" width="160" align="center">
        <template slot-scope="scope">
          <div v-if="scope.row.listPictureName === null || scope.row.listPictureName.length === 0"></div>
          <div v-else>
            <el-button size="small" @click="handleViewPicture(scope.$index, scope.row)"></el-button>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="video" label="" width="160" align="center">
        <template slot-scope="scope">
          <div v-if="scope.row.listVideoName === null || scope.row.listVideoName.length === 0"></div>
          <div v-else>
            <el-button size="small" @click="handleViewVideo(scope.$index, scope.row)"></el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <el-col :span="24" class="toolbar">
      <el-pagination
        layout="prev, pager, next"
        @current-change="handleCurrentChange"
        :page-size="10"
        :total="total"
        style="float:right;"
      ></el-pagination>
    </el-col>

    <el-dialog title="" v-model="videoFormVisible" :close-on-click-modal="false">
      <div class="video-box">
        <video-player
          class="video-player vjs-custom-skin"
          ref="videoPlayer"
          :playsinline="true"
          :options="playerOptions"
        ></video-player>
      </div>
    </el-dialog>

    <el-dialog title="" v-model="picFormVisible" :close-on-click-modal="false">
      <div class="picture-box">
        <el-carousel :interval="4000" type="card" height="400px" align="center">
          <el-carousel-item v-for="item in this.picForm.listPictureName">
            <img :src="item" height="400" />
          </el-carousel-item>
        </el-carousel>
      </div>
    </el-dialog>
  </section>
</template>

<script>
import util from "../../common/js/util";
import {
  apiGetGateEventPage,
  apiGetGateEventList,
  apiTestAesDecrypt
} from "../../api/api";
import { API_HOST } from "../../api/api";

export default {
  data() {
    return {
      filters: {
        name: ""
      },
      isAdmin: 0,
      gateEvents: [],
      showTemp: false,
      total: 0,
      page: 1,
      listLoading: false,
      videoFormVisible: false,
      videoForm: {},
      picFormVisible: false,
      picForm: {},
      playerOptions: {
        live: true,
        playbackRates: [0.7, 1.0, 1.5, 2.0], //
        autoplay: true, //true,
        muted: false, // 
        loop: false, // 
        preload: "auto", // <video>auto,
        language: "zh-CN",
        aspectRatio: "16:9", //  - "16:9""4:3"
        fluid: true, // trueVideo.js player
        sources: [
          {
            type: "",
            src: ""
            //"../../../static/20190529111256.mp4" //url
          }
        ],
        poster: "", //"../../static/image/thumb_1_1380_460_20181009043014983386.jpg", //
        // width: document.documentElement.clientWidth,
        notSupportedMessage: "", //Video.js
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: true //
        }
      }
    };
  },
  methods: {
    formatTime: function(row, column) {
      return util.formatDate.format(
        new Date(row.srcTs * 1000),
        "yyyy-MM-dd hh:mm:ss"
      );
    },

    handleCurrentChange: function(val) {
      this.page = val;
      this.getGateEvents();
    },

    getGateEvents: function() {
      let para = {
        page: this.page,
        count: 10,
        filter: this.filters.name
      };
      this.listLoading = true;
      apiGetGateEventPage(para).then(res => {
        this.total = res.data.myModel.total;
        this.gateEvents = res.data.myModel.data;
        this.listLoading = false;
        this.gateEvents.forEach(e => {
          e.homeName =
            (e.userBuildingNum || "?") +
            "" +
            (e.userUnitNum || "?") +
            "" +
            (e.userRoomNum || "?") +
            "";
          e.deviceName =
            (e.deviceBuildingNum || "?") +
            "" +
            (e.deviceUnitNum || "?") +
            "";
          e.lockCat = e.lockCat === "1" ? "" : "";
          e.media =
            e.media === "00"
              ? ""
              : e.media === "01"
              ? ""
              : e.media === "02"
              ? ""
              : "";
          console.log(
            "e.envTemp:" +
              JSON.stringify(e.envTemp) +
              " e.humanTemp:" +
              JSON.stringify(e.humanTemp)
          );
          if (
            (e.envTemp != null && e.envTemp !== "") ||
            (e.humanTemp != null && e.humanTemp != "")
          ) {
            this.showTemp = true;
          }
        });
      });
    },
    handleViewVideo: function(index, row) {
      this.videoFormVisible = true;
      this.videoForm = Object.assign({}, row);
      this.playerOptions.sources[0].src = API_HOST + row.listVideoName[0];
    },
    handleViewPicture: function(index, row) {
      this.picFormVisible = true;
      this.picForm = Object.assign({}, row);
      this.picForm.listPictureName = [];
      for (let i = 0; i < row.listPictureName.length; i++) {
        this.picForm.listPictureName[i] = API_HOST + row.listPictureName[i];
      }
    }
  },

  mounted() {
    this.getGateEvents();
    this.isAdmin = parseInt(sessionStorage.getItem("isAdmin"));
  }
};
</script>

<style scoped>
</style>



